h1 {
  color: $light-txt-low;
  font-family: "League Spartan", sans-serif;
  font-size: 72px;
  margin-bottom: 20px;
  @media (max-width: 1000px) {
    font-size: 64px;
  }
}

h2 {
  color: $light-txt-low;
  font-family: "League Spartan", sans-serif;
  font-size: 64px;
  text-align: center;
  @media (max-width: 1200px) {
    font-size: 56px;
  }
}

h3 {
  font-size: 36px;
  text-align: center;
}
h4 {
  font-size: 24px;
  font-weight: 400;
}

.section__error {
  height: 800px;
}

