  h1 {
    color: $dark-txt-low;
  }
  h2 {
    color: $dark-txt-low;
  }
  header {
    background-color: $dark-scnd-bg;
    border-bottom: 3px solid $dark-bdr-non-int;
    color: $dark-gray-txt-low;
    button {
      color: $dark-txt-low;
    }
    button:hover {
      background-color: $dark-comp-bg-hover;
    }
    button:active {
      background-color: $dark-comp-bg-selected;
    }
    li:hover {
      color: $dark-txt-low;
    }
  }
  .section__intro {
    button {
      background-color: $dark-comp-bg-normal;
      border: 2px solid $dark-bdr-int-normal;
      color: $dark-gray-txt-low;
    }
    button:hover {
      color: $dark-txt-low;
      background-color: $dark-comp-bg-hover;
      border: 2px solid $dark-bdr-int-hover;
    }
    button:active {
      background-color: $dark-comp-bg-selected;
    }
    img {
      border: 3px solid $dark-bdr-non-int;
    }
  }
  .group-skills {
    div {
      background-color: $dark-gray-comp-bg-normal;
      border: 1px solid $dark-gray-bdr-non-int;
      ul {
        color: $dark-gray-txt-low;
      }
    }
  }
  .group__projects {
    article {
      border: 2px solid $dark-gray-bdr-int-normal;
      background-color: $dark-gray-comp-bg-normal;
    }
    article:hover {
      background-color: $dark-comp-bg-hover;
      border: 2px solid $dark-bdr-int-hover;
    }
  }
  footer {
    background-color: $dark-scnd-bg;
    border-top: 3px solid $dark-bdr-non-int;
  }
}
